.container {
  height: calc(100vh - 12vh);
  /* background: #fff; */
  display: flex;
  justify-content: center;
  flex-direction: column;

  position: relative;
  background: transparent;
}
.bgImage {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  object-fit: scale-down;
  opacity: 0.5;
}
.bgtext {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.headertext {
  font-size: 54px;
  font-weight: 700;
  color: var(--background);
  text-align: center;
}
.subtext {
  font-size: 6vw;
  font-weight: 700;
  color: var(--background);
  text-align: center;
  border: 2px solid #00587a;
  /* width: 525px; */
  padding: 4px;
  border-radius: 12px;
  margin-bottom: 18px;
}
.button {
  font-size: 18px;
  color: #fff;
  /* font-weight: 700; */
  background-color: var(--accent2);
  /* padding: 8px 12px; */
  /* display: block; */
  width: 200px;
  padding: 12px;
  border-radius: 4px;
  text-align: center;
}
.button1 {
  font-size: 18px;
  color: #fff;
  /* font-weight: 700; */
  color: var(--accent2);
  border: 2px solid var(--accent2);
  /* padding: 8px 12px; */
  /* display: block; */
  width: 200px;
  padding: 12px;
  border-radius: 4px;
  text-align: center;
  margin-left: 12px;
  margin-top: 12px;
}
.groupbuttons {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
@media screen and (min-width: 800px) {
  .container {
    height: calc(100vh - 12vh);
    /* background: #fff; */
    display: flex;
    justify-content: center;
    flex-direction: column;
    position: relative;
    background: transparent;
    overflow: hidden;
  }
  .groupbuttons {
    all: unset;
    display: flex;
  }
  .bgImage {
    all: unset;
    position: absolute;
    object-fit: scale-down;
    /* margin-left: 265px; */
    transform: scale(0.7);
    opacity: 1;
    left: 340px;
    z-index: -1;
  }

  .bgtext {
    all: unset;
    width: 50vw;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .headertext {
    font-size: 54px;
    font-weight: 700;
    color: var(--background);
    text-align: center;
  }
  .subtext {
    font-size: 34px;
    font-weight: 700;
    color: var(--background);
    text-align: center;
    border: 2px solid #00587a;
    /* width: 525px; */
    padding: 8px;
    border-radius: 12px;
    margin-bottom: 18px;
  }
  .button {
    font-size: 18px;
    color: #fff;
    /* font-weight: 700; */
    background-color: var(--accent2);
    /* padding: 8px 12px; */
    /* display: block; */
    width: 200px;
    padding: 12px;
    border-radius: 4px;
    text-align: center;
    cursor: pointer;
  }
  .button1 {
    font-size: 18px;
    color: #fff;
    /* font-weight: 700; */
    color: var(--accent2);
    border: 2px solid var(--accent2);
    /* padding: 8px 12px; */
    /* display: block; */
    width: 200px;
    padding: 12px;
    border-radius: 4px;
    text-align: center;
    margin-left: 12px;
    margin-top: 0px;
    cursor: pointer;
  }
}
