.package {
  width: 60vw;
}
.parent_container {
  display: grid;
  place-content: center;
  margin-top: 50px;
}
.subcontainer {
  height: 600px;
  /* height: 30vh; */
  overflow-y: auto;
}
.packagelist {
  display: flex;
}
/* .package table td {
  border: 1px solid black;
  padding: 4px 8px;
} */
.tab_element {
  cursor: pointer;
  width: 50%;
  text-align: center;
  border: 1px solid;
}
.tab_element:hover {
  background: rgba(76, 76, 76, 0.5);
}
.tab--active {
  color: #fff;
  background: var(--background);
  width: 50%;
  text-align: center;
}

.button1,
.button2 {
  background: var(--accent2);
  padding: 2px 4px;
  border-radius: 4px;
  cursor: pointer;
  color: #fff;
}
.button1 {
  color: var(--accent2);
  border: 2px solid var(--accent2);
  background: none;
  margin: 0px 8px;
}
.nav-toggle {
  display: none;
}
.nav-toggle:checked ~ .packagedetails {
  background-color: red;
  height: 200px;
  transform: scale(1, 1);
}
.packagedetails {
  transform: scale(1, 0);
  transform-origin: top;
  transition: transform 300ms ease-in-out;
}
.tab {
  display: flex;
  justify-content: space-around;
}
@media screen and (max-width: 800px) {
  .package {
    width: 85vw;
  }

}