.container table tr {
  border: 1px solid black;

  display: flex;
  justify-content: space-between;
}

.container table td {
  padding: 4px 8px;
}
.container table {
  border-collapse: collapse;
  width: 59vw;
}
.container {
  height: 400px;
}
.button2 {
  background: var(--accent2);
  padding: 2px 4px;
  border-radius: 4px;
  cursor: pointer;
  color: #fff;
  margin-right: 4px;
}
.input_search {
  width: 100%;
  height: 32px;
  border-radius: 4px;
  /* border: none; */
  margin: 12px 0px;
}
@media screen and (max-width: 800px) {
  .container table td:nth-child(odd) {
    width: 70%;
  }
  .container table td:nth-child(odd) {
    width: 30%;
  }
  .button2 {
    width: 100px;
  }
  .container table tr {
    width: 85vw;
  }
}
