.container h2,
h1 {
  color: var(--heading);
}
.container {
  background: #fff;
  gap: 60px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 32px 0px;
  margin-top: 48px;
  position: relative;
}
.form {
  background: var(--secondary);
  padding: 32px;
  border-radius: 4px;
  position: relative;
}
.field {
  margin-bottom: 18px;
  display: flex;
  flex-direction: column;
}
.field input {
  height: 32px;
  border-radius: 4px;
  border: none;
  width: 300px;
}
.input--error {
  height: 32px;
  border-radius: 4px;
  width: 300px;
  border: 2px solid #e8505b !important;
}
.input--disabled {
  user-select: none;
  pointer-events: none;
}
.field select,
option {
  height: 32px;
  border-radius: 4px;
  border: none;
  width: 300px;
}
.contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 32px;
}
.button {
  font-size: 18px;
  color: #fff;
  /* font-weight: 700; */
  background-color: var(--accent2);
  /* padding: 8px 12px; */
  /* display: block; */
  width: 200px;
  padding: 12px;
  border-radius: 4px;
  text-align: center;
  cursor: pointer;
}
.errortext {
  font-size: 12px;
  color: #e8505b;
}
.groupcontact {
  display: flex;
  flex-direction: column;
  position: relative;
}
.aboutimg {
  width: 100%;
  height: 100%;
  position: absolute;
  /* bottom: 59px; */
  /* left: -77px; */
  opacity: 0.5;
  object-fit: contain;
  bottom: -280px;
}
@media screen and (min-width: 800px) {
  .aboutimg {
    all: unset;

    width: 55vw;
    position: absolute;
    bottom: 59px;
    left: -77px;
    opacity: 0.5;
    transform: scale(1);
  }
  .container {
    all: unset;
    height: 100vh;
    background: #fff;
    gap: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 48px;
    position: relative;
  }
  .groupcontact {
    display: flex;
    position: relative;
  }
  .input--error {
    height: 32px;
    border-radius: 4px;
    width: 300px;
    border: 2px solid #e8505b !important;
  }
  .form {
    background: var(--secondary);
    padding: 32px;
    border-radius: 4px;
    position: relative;
  }
  .field {
    margin-bottom: 18px;
    display: flex;
    flex-direction: column;
  }
  .field input {
    height: 32px;
    border-radius: 4px;
    border: none;
    width: 300px;
  }
  .contact {
    all: unset;
    display: flex;
    gap: 32px;
    flex-direction: column;
  }
}
