.container {
  height: 300px;
}
.container table tr {
  border: 1px solid black;
  display: flex;
  justify-content: space-between;
}
.container table td {
  padding: 4px 8px;
}
.container table {
  border-collapse: collapse;
  width: 55vw;
}
.button2 {
  background: var(--accent2);
  padding: 2px 4px;
  border-radius: 4px;
  cursor: pointer;
  color: #fff;
  margin-right: 4px;
}
.input_search {
  width: 100%;
  height: 32px;
  border-radius: 4px;
  /* border: none; */
}
